import React from 'react';

import { DashboardWrapper } from '@domains/dashboard/styled';

import { Link } from 'react-router-dom';

import { Tiles } from '@daimler/react-ux-business-app';

import { Path } from '@router/types';

import { CardContainer } from './styled';
import { setCurrentMandantId } from '@mandants/index';
import { Mandant } from '@api/generated/apiClient.generated';

type MandantsDomainProps = {
  mandants: Mandant[];
};

export const MandantsDomain: React.FC<MandantsDomainProps> = ({ mandants }) => {
  return (
    <DashboardWrapper>
      <CardContainer>
        {mandants.map((mandant) => (
          <Link
            to={Path.DASHBOARD}
            key={mandant.id}
            onClick={() => setCurrentMandantId(mandant.id.toString())}
          >
            <Tiles
              title={`${mandant.id} - ${mandant.name}`}
              description={`${mandant.longDescription} ( ${mandant.shortDescription} )`}
            />
          </Link>
        ))}
      </CardContainer>
    </DashboardWrapper>
  );
};
