import React, { memo } from 'react';

import { MandantsDomain } from '@domains/mandants';
import { useMandants } from '@mandants/index';

const MandantsPage = () => {
  const mandants = useMandants();

  if (!mandants) return null;

  return <MandantsDomain mandants={mandants} />;
};

export default memo(MandantsPage);
